<template>
	<div>
		<van-nav-bar
		  left-text="logo"
		  fixed
		/>		

		<van-button type="default" class="btn" @click="tip" size='small'>签到</van-button>
		<van-search placeholder="搜索你想知道的"  shape='round'/>
		<van-popup v-model="show"  :overlay="true" class='tan'><h4>{{str}}</h4></van-popup>
		<van-swipe :autoplay="3000" :height="200" :width="450">
		  <van-swipe-item v-for="(image, index) in images" :key="index">
		    <img :src="image.banner_img_url" />
		  </van-swipe-item>
		</van-swipe>
		<div class="mid">
			<van-button type="info" class='zhuan'>组织入住</van-button>
			<van-button type="info" class='zhuan'>成为志愿者</van-button>
		</div>
		<van-row type="flex">
		  <van-col span="6">扶贫助困</van-col>
		  <van-col span="6">敬老助老</van-col>
		  <van-col span="6">关爱儿童</van-col>
		  <van-col span="6">社区便民</van-col>		  
		</van-row>
		<van-row type="flex">
		  <van-col span="6">教育科普</van-col>
		  <van-col span="6">绿色环保</van-col>
		  <van-col span="6">文化活动</van-col>
		  <van-col span="6">全部活动</van-col>		  
		</van-row>
		<router-link to='' tag='h4'>最新招募</router-link>
		<van-card
			v-for="item in list"
		  :price="item.pprice"
		  :desc="item.pdesc"  
		  :title="item.pname"
		  :thumb="item.pimg"
		  @click="detail(item.pid)"
		/>
		<router-link to=''  tag='h4'>公益项目</router-link>
		<div v-for="item in list"  class="ex">
			<img :src='item.pimg' >
			<span>{{item.pname}}</span>
		</div>
	</div>
</template>

<script>
//	import Head from '@/components/Head'		
	import axios from 'axios'
	export default{
		name:'Main',
//		components:{
//			Head
//		},
		data(){
			return{
				title:'首页',
				images:[],
				list:[],
				show:false,
				str:'',
				bool:true
				
			}
		},
		mounted(){
			this.$emit('toTitle',this.title)
			var _this=this;
//			轮播
			axios({
				
				url:'http://jx.xuzhixiang.top/ap/api/bannerlist.php',
				params:{uid:1017}
			}).then((data)=>{
				_this.images=data.data.data
			})
//			数据列表
			axios({
				url:'http://jx.xuzhixiang.top/ap/api/productlist.php',
				params:{uid:6295}
			}).then((data)=>{
				_this.list=data.data.data
			})
		},
		methods:{
			detail(id){
				this.$router.push({name:'Prodetail',query:{id:id}})
			},
			tip(){
				this.show = true;
				axios({
					method:'get',
					//headers:{'Content-Type':'application/x-www-form-urlencoded'},
					url:'http://47.107.138.115:8082/weishan-1.0/weishan.weishan/signin/signin.do',
					params:{
					  "location": "郑州",
					  "uid": this.$store.state.uid
					}
					// params:{uid:1}
				}).then((data)=>{
					console.log(data);
					if(this.$store.state.uid){
						this.str = data.data.msg
					}else{
						this.str = '未登录'
					}
					
				})
			}
		}
	}
</script>

<style scoped="">
	img{
		width: 100%;
		height: 100%;
	}
	.van-swipe__track{
		width: 100%;
		height: 200px;
	}
	.btn{
		position: absolute;
		right: 10px;
		top: 10px;
		z-index: 20;
		border:none;
	}
	.tan{
		position:fixed;
		width: 150px;
		height: 150px;
		left: 50%;
		top: 20%;
		border-radius: 50%;
		text-align: center;
		line-height: 100px;
	}

	.van-search{
		margin-bottom: 0;
		/*margin-top: 46px;*/
	}
	.mid{
		height: 70px;
		vertical-align: middle;
	}
	.zhuan{
		margin-top: 10px;
		margin-left: 60px;
	}
	.van-col{
		height: 100px;
		text-align: center;
		line-height: 100px;
		font-size: 12px;
	}
	.ex{
		margin: 10px 0;
		padding: 20px;
	}
	.ex img{
		width: 100%;
		height: 100px;
	}
</style>


