import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Home from '@/views/Home'
import Main from '@/views/Main'
import Trends from '@/views/Trends'
import Publish from '@/views/Publish'
import Mine from '@/views/Mine'
import Login from '@/views/Login'
import Register from '@/views/Register'
import Relogin from '@/views/Relogin'
import Prodetail from '@/views/Prodetail'
import Search from '@/views/Search'
import Recruit from '@/views/Recruit'
import Enter from '@/views/Enter'




Vue.use(Router)

export default new Router({
  routes: [
  	{
      path: '/',
      redirect:'/login',
      name: 'Index',
      component: Index,
      children:[
      	{
      		path:'/login',
      		name:'Login',
      		component:Login
      	},
      	{
      		path:'/register',
      		name:'Register',
      		component:Register
     	 },{
				path:'/relogin',
				name:'Relogin',
				component:Relogin
				}
      ]
    },
    {
			path:'/home',
			name:'Home',
			redirect:'/main',
			component:Home,
			children:[
				{
					path:'/main',
					name:'Main',
					component:Main
				}
				,
				{
					path:'/trends',
					name:'Trends',
					component:Trends
				},{
					path:'/publish',
					name:'Publish',
					component:Publish
				},{
					path:'/mine',
					name:'Mine',
					component:Mine
				}
			]
    },
    {
    	path:'/prodetail',
			name:'Prodetail',
			component:Prodetail
    },{
    	path:'/search',
    	name:'Search',
    	component:Search
    },{
			path:'/recruit',
			name:'Recruit',
    	component:Recruit
		},{
			path:'/enter',
			name:'Enter',
			component:Enter
		}
  ]
})



